.container {
  width: 100%;
  max-width: 1240px;
  margin: 0 auto;
  padding: 0 40px;

  @include vp-1199 {
    max-width: 1050px;
    padding: 0 45px;
  }

  @include vp-767 {
    min-width: 320px;
    max-width: 620px;
    padding: 0 15px;

    &--fullscreen {
      max-width: 100%;
      padding: 0;
    }
  }
}
